@import url(https://fonts.googleapis.com/css?family=Roboto);
body {
  margin: 1.5rem;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
.song-name{
  color: red;
}

.main_body{
  padding-top: 80px;
}


.tweet {
    border: 1px solid #ccc;
    width: 564px;
    min-height: 68px;
    padding: 10px;
    display: -webkit-flex;
    display: flex;
    font-family: "Helvetica", arial, sans-serif;
    font-size: 14px;
    line-height: 18px;
}

.avatar {
    width: 48px;
    height: 48px;
    border-radius: 5px;
    margin-right: 10px;
}

.name {
    font-weight: bold;
    margin-bottom: 0.5em;
    margin-right: 0.3em;
}

.handle {
    color: #8899a6;
    font-size: 13px;
}

.time {
    padding-left: 0.3em;
    color: #8899a6;
}
.time::before {
    content: "\00b7";
    padding-right: 0.3em;
}
.buttons {
    margin-top: 10px;
    margin-left: 2px;
    font-size: 1.4em;
    color: #aab8c2;
}
/* .buttons i {
    width: 80px;
} */

.reply-button, .retweetbutton,.likebutton, .more-options-button {
    width: 80px;
    display: inline-block;
}
.like-count, .retweet-count {
    position: relative;
    bottom: 2px;
    font-size: 13px;
    margin-left: 6px;
    font-weight: bold;
}
.nav-item:hover{
    background-color: gray;
    opacity: 80%;
}
.filelist {
    font-family: Helvetica, sans-serif;
    width: 980px;
    color: #333;
    margin: 0 auto;
    border: 1px solid #ccc;
    border-collapse: collapse;
}

.filelist td {
    border-top: 1px solid #ccc;
}

.file-name {
    padding: 4px;
    max-width: 180px;
}

.file-icon {
    width: 17px;
    padding-left: 4px;
}

.file-icon .fa-folder {
    color: #508FCA;
}

.commitmessage {
    max-width: 442px;
    padding-left: 10px;
    overflow: hidden;
}

.age {
    width: 125px;
    text-align: right;
    padding-right: 4px;
}
body {
  margin: 0;
  color: #1b1a20;
  font-family: 'Roboto', sans-serif;
}

.shelf-item {
  min-width:200px;
  position: relative;
  text-align: center;
  box-sizing: border-box;
  padding: 10px;
  margin-bottom: 30px;
  border: 1px solid transparent;
  cursor: pointer;
  border: 1px solid #eee;
  }

.shelf-item:hover {
border: 1px solid rgb(17, 17, 17);
}

.shelf-item:hover .shelf-item__buy-btn {
  background-color: #eabf00;
}

.shelf-stopper {
position: absolute;
color: #ececec;
top: 10px;
right: 10px;
padding: 5px;
font-size: 0.6em;
background-color: #1b1a20;
cursor: default;
}

.shelf-item_thumb_img {
  width: 100%;
  height:50px;
}

.shelf-item__price {
height: 60px;
}

.shelf-item.val b {
font-size: 1.5em;
margin-left: 5px;
}

.shelf-item.installment {
color: #9c9b9b;
}

.shelf-item__buy-btn {
background-color: #1b1a20;
color: #fff;
padding: 15px 0;
margin-top: 10px;
cursor: pointer;
border-bottom: 2px solid #151419;
transition: background-color 0.2s;
}

.shelf-item__title{
  position: relative;
  margin-top:10px;
  padding: 0 20px;
  height: 45px;
}

.shelf-item__title::before {
  content: '';
  width: 20px;
  height: 2px;
  background-color: #eabf00;
  position: absolute;
  bottom: 0;
  left: 50%;
  margin-left: -10px;
}

@media only screen and (min-width:460px){
  .shelf-item{
    width:50%
  }
}

@media only screen and (min-width:768px){
  .shelf-item{
    width:33.33%
  }
}

@media only screen and (min-width:1024px){
  .shelf-item{
    width:25%
  }
}



.ItemPage-items {
    margin: 0;
    padding: 0;
    }
    .ItemPage-items li {
    list-style: none;
    margin-bottom: 20px;
    }
.Item {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    border-bottom: 1px solid #ccc;
    padding: 10px;
    }
    .Item-image {
    width: 64px;
    height: 64px;
    border: 1px solid #ccc;
    margin-right: 10px;
    float: left;
    }
    .Item-title {
    font-weight: bold;
    margin-bottom: 0.4em;
    }
    .Item-price {
    text-align: right;
    font-size: 18px;
    color: #555;
    }
    .Item-addToCart {
    margin-bottom: 5px;
    font-size: 14px;
    border: 2px solid #FFAA3F;
    background-color: #fff;
    border-radius: 3px;
    cursor: pointer;
    }
    .Item-addToCart:hover {
    background-color: #FFDDB2;
    }
    .Item-addToCart:active {
    background-color: #ED8400;
    color: #fff;
    outline: none;
    }
    .Item-addToCart:focus {
    outline: none;
    }
    .Item-left {
    -webkit-flex: 1 1;
            flex: 1 1;
    }
    .Item-right {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    }
.CartPage-items {
    margin: 0;
    padding: 0;
    }
    .CartPage-items li {
    list-style: none;
    }
    .CartItem-count {
    padding: 5px 10px;
    border: 1px solid #ccc;
    }
    .CartItem-addOne,
    .CartItem-removeOne {
    padding: 5px 10px;
    border: 1px solid #ccc;
    background: #fff;
    }
    .CartItem-addOne {
    border-left: none;
    }
    .CartItem-removeOne {
    border-right: none;
    }
